import './App.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import AfterInfo from "./components/AfterInfo";
import Beginning from "./components/Beginning";
import Ending from "./components/Ending";
import {
  BrowserRouter as Router,
  Switch,
  Route,
} from "react-router-dom";

function App() {
  return (
    <div className="App">
      <Router>
          <Switch>
            <Route exact path="/">
              <AfterInfo />
            </Route>
            <Route exact path="/get-info">
              <Beginning />
            </Route>
            <Route exact path="/the-end">
              <Ending />
            </Route>
          </Switch>
      </Router>
    </div>
  );
}

export default App;
